.profile__name {
  font-size: 42px;
  line-height: 1.14;
  font-weight: 500;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-rendering: optimizeLegibility;
}

@media screen and (max-width: 879px) {
  .profile__name {
    font-size: 27px;
    text-align: center;
    line-height: 1.21;
  }
}
